import BN from "bn.js";
import EC, { curve } from "elliptic";

import { EllipticCurve } from "./interface";

export class Ed25519Curve extends EC.ec implements EllipticCurve {
  constructor() {
    super("ed25519");
  }

  bufferToScalar(b: Buffer): BN {
    return new BN(b, "le");
  }

  generateScalar(): BN {
    return this.genKeyPair().getPrivate();
  }

  scalarToBuffer(s: BN, buf: typeof Buffer): Buffer {
    s.toString("hex");
    return s.toArrayLike(buf, "le", 32);
  }

  pointToBuffer(p: curve.base.BasePoint, buf: typeof Buffer): Buffer {
    // Each y has 2 possible x-values, x and -x. When compressing, it's enough to
    // store y and use the last byte to encode the sign of x.
    const x = p.getX();
    const y = p.getY();
    const b = y.toArrayLike(buf, "le", 32);
    b[b.length - 1] |= x.and(new BN(1)).eqn(1) ? 0x80 : 0x00;
    return b;
  }
}
